.Doc-yellowpaper {
  text-align: left;
  padding: 0px 80px;
}

.Doc-whitepaper {
  padding: 0px 20px;
  background-color: #fff;
  color: #16181c;
  font-size: calc(10px + 1vmin);
}
.Doc-whitepaper-listItem {
  text-align: left;
}
.Doc-whitepaper-listItem ol {
  padding-left: 20px;
}
.Doc-whitepaper-paragraph {
  text-align: left;
}
.Doc-whitepaper-introduction {
  padding: 0px 100px;
}

