/* Override Mui */
.Mui-expanded {
  color: #317aff;
  margin: 0 5px;
}
.Mui-expanded #expandIcon {
  color: #ffffff;
}
.MuiDrawer-root {
  border-right: 1px solid #fff;
}
.MuiDrawer-paper {
  border-right: 1px solid #606770 !important;
}
.MuiToolbar-root {
  padding: 0px 20px !important;
}
.MuiList-root {
  padding: 8px 5px !important;
}
.MuiListItemButton-root {
  border-radius: 8px !important;
}
.MuiListItemText-root {
  word-break: break-word;
  color: #ffffff;
  font-family: "Manrope-bold";
}
.Mui-selected {
  background-color: #25272b;
}
.Mui-selected .MuiTypography-root {
  color: #317aff !important;
}
h1 {
  margin-block-start: 0px !important;
  margin-block-end: 8px !important;
}
p {
  line-height: 1.5;
  word-break: keep-all;
}
li {
  line-height: 1.5;
}
a {
  color: #5a94ff;
  padding: 0px 2px;
}
code {
  background-color: #f6f8fa;
  padding: 8px 8px;
  line-height: 1.5;
  border-radius: 8px;
  color: #000;
  display: block;
  white-space: pre-wrap;
  overflow-x: scroll;
  max-height: 200px;
  overflow-y: scroll;
}
table,
th,
td {
  border: 1px solid;
  padding: 0.75rem;
}
table {
  scroll-behavior: smooth;
  table-layout: fixed;
  border-collapse: collapse;
  border: 0.5px solid #dadde1;
  padding: 0.75rem;
  line-height: 1.5;
  width: 100%;
  word-break: break-word;
}
table thead tr {
  border: 2px solid #dadde1 !important;
  border-bottom: 2px !important;
  border-bottom-color: #dadde1 !important;
  border-bottom-style: solid !important;
}

em,
a {
  word-break: break-all;
}
h1 {
  word-break: break-word;
}
ol {
  padding-left: 16px;
}

.CustomComp {
  margin: 0 auto;
}

.App-footer {
  padding: 30px;
  text-align: left;
  color: white;
}

