@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;600;700&display=swap');

body {
  margin: 0;
  font-family: Manrope-regular, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Minima Default Font - Manrope */
@font-face {
  font-family: Manrope-regular;
  src: url('../src/assets/fonts/Manrope/Manrope-Regular.ttf');
}
/* Minima Default Font - Manrope */
@font-face {
  font-family: Manrope-Bold;
  src: url('../src/assets/fonts/Manrope/Manrope-Bold.ttf');
}

.title-bar {
  display: flex;
  align-items: center;
  background: black;
  height: 54px;
  padding: 0 16px;
  color: white;
  user-select: none;
  font-weight: bold;
  position: sticky;
  top: 0;
  z-index: 100;
  font-family: 'Manrope', sans-serif;
}

.title-bar__brand {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.title-bar__icon {
  margin-left: -4px;
  margin-right: 16px;
}

.title-bar__title {
  font-size: 18px;
  font-weight: bold;
  margin-left: 14px;
}

